/* Surprise Animation */
.cocktailLoader {
   position: relative;
   width: 200px;
   height: 200px;
   margin: 2rem;
   background-color: #36454f;
   /* background: linear-gradient(89.85deg, #FFF3B2 4.81%, #F3A78E 90.74%); */

   background: linear-gradient(to right, #3ab795, #447d8c);

   border-radius: 20px;
   cursor: pointer;
}

#loader {
   position: absolute;
   top: 20%;
   left: 36%;
   height: 100px;
   width: 50px;
}

#glass {
   position: relative;
   height: 100%;
   background: rgba(255, 255, 255, 0.1);
   border-radius: 0% 0% 15% 15%;
   border: 3px solid;
   border-top: 0;
   border-bottom: 10px solid;
   border-color: rgba(255, 255, 255, 0.7);
   overflow: hidden;
}
#drink {
   position: absolute;
   top: 100%;
   right: 0;
   bottom: 0;
   left: 0;
   background: linear-gradient(to bottom, yellow, orangered);
   box-shadow: inset 0 2px 1px rgba(255, 69, 0, 0.2);
   opacity: 0.7;
}
#counter {
   position: relative;
   line-height: 100px;
   font-size: 11px;
   color: rgba(255, 255, 255, 1);
}

#lemon {
   position: absolute;
   top: 0;
   right: 0;
   height: 40px;
   width: 40px;
   margin-top: -19px;
   margin-right: -19px;
   background: radial-gradient(#f7f3b6 10%, #d7d26c);
   border-radius: 50%;
   border: 2px solid #47582e;
   box-shadow: inset 0 0 0 2px #f7f3b6;
   transition: opacity 0.5s;
   opacity: 0;
}
#straw {
   position: absolute;
   bottom: 20px;
   right: 30%;
   height: 110px;
   width: 6px;
   background: #303030;
   border-radius: 0 6px 0 0;
   transform: rotate(-18.5deg);
   transform-origin: left bottom;
   -webkit-transform: rotate(-18.5deg);
   -webkit-transform-origin: left bottom;
   opacity: 0;
}
#straw:after {
   content: '';
   position: absolute;
   top: 0;
   right: 0;
   height: 6px;
   width: 40px;
   background: inherit;
   border-radius: 0 6px 0 0;
}

#cubes {
   position: absolute;
   top: 0;
   right: 0;
   bottom: 0;
   left: 0;
}
#cubes div {
   /*display: none;*/
   position: absolute;
   width: 25px;
   height: 25px;
   background: rgba(255, 255, 255, 0.3);
   border-radius: 5px;
   box-shadow: inset 0 0 10px rgba(255, 255, 255, 0.6);
}
#cubes div:nth-child(1) {
   bottom: 0;
}
#cubes div:nth-child(2) {
   bottom: 22.5px;
   left: 12.5px;
   transform: rotate(32deg);
   transform-origin: center bottom;
