.muliTitle {
   /* padding:20px;
   margin-top:30px; */
   margin-left: 40px;
}
.custom-hr {
   justify-content: center;
   align-items: center;
   height: 1.5px;
   /* border: 0.001px solid black; */
   background-color: black;
}
.multiSelected {
   margin-left: 30px;
   margin-left: 20px;
   font-family: 'Montserrat', sans-serif;
}
.selectBox {
   /* background: #FDF9F9;  */
   background-color: #fff4e7;
   box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
   border-radius: 41px;
   margin-top: 50px;
   border: 1px solid gray;
   box-shadow: 5px 5px 15px 5px rgb(0 0 0 / 10%);
   
}

.multiInputfield {
   margin: 0 auto;
   padding: 30px 24px;
}

#inputField {
   background: #ffffff;
   border: 1px solid #000000;
   width: 100%;
   padding: 10px 11px 11px 11px;
   border-radius: 16px;
   box-shadow: none;
   outline: none;
   margin: 0;
   box-sizing: border-box;
}

#listIngredient {
   overflow-y: scroll;
   height: 800px;
   box-shadow: darkgrey;
   padding: 16px;
   text-align: center;
   scrollbar-width: thin; /* "auto" or "thin" */
   scrollbar-color: #fdbcbf white;
}
#listIngredient::-webkit-scrollbar {
   width: 10px; /* width of the entire scrollbar */
}

#listIngredient::-webkit-scrollbar-track {
   background: white; /* color of the tracking area */
}

#listIngredient::-webkit-scrollbar-thumb {
   background-color: #fdbcbf; /* color of the scroll thumb */
   border-radius: 10px; /* roundness of the scroll thumb */
   /* border: 2px solid gray;   */
   /* creates padding around scroll thumb */
}
.ingredientImage {
   filter: contrast(140%);
}

.chipMain {
   display: inline-block;
   padding: 0 12px;
   height: 25px;
   font-size: 12px;
   line-height: 25px;
   border-radius: 25px;
   border: 0.3px solid black;

   background: linear-gradient(
      89.83deg,
      #fdd1d3 -3.08%,
      rgba(253, 209, 211, 0) 126.09%
   );
   /* background: linear-gradient(
    30.11deg,
    #ffffff -11.43%,
    #ffee93 13.8%,
    #f0d7bf 47.62%
  ); */

   margin: 2px;
   cursor: pointer;
   transition: 0.65s ease-in-out;
}
.chipMain:hover {
   background-color: #ffd977;
   border: 0.6px solid black;
}
.chipActive {
   background-color: #ffd977;
   border: 1.2px solid black;
}

.chip {
   display: inline-block;
   padding: 0 25px;
   height: 30px;
   font-size: 16px;
   line-height: 30px;
   border-radius: 25px;

   background: linear-gradient(
      180deg,
      #ffe193 39.6%,
      rgba(255, 225, 147, 0) 129.7%
   );

   margin: 2px;
}

.multiCardList {
   display: flex;
   flex-wrap: wrap;
   font-size: 5px;
   justify-content: space-around;
   font-family: 'Lato', sans-serif;
   margin-top: 70px;
}
/* 
 Card CSS */
.multiCardImg {
   height: 160px;
   border-radius: 20px;
   /* border: 1px solid white; */
   filter: contrast(125%);
   box-shadow: 5px 5px 15px 5px rgb(0 0 0 / 30%);
   transition: all 0.25s ease-in-out;
}
.multiCardImg:hover {
   transform: scale(1.13);
}

.multidrinkName {
   font-family: 'Montserrat', sans-serif;
   font-weight: 500;
   font-size: 20px;
   margin: 5px 0 40px 0;
   width: 200px;
   cursor: pointer;
   transition: font-weight 0.7s ease-in-out;
}
.multidrinkName:hover {
   font-weight: 600;
}
.secondhalfMulti {
   height: 1000px;
   overflow-y: scroll;
   /* background: linear-gradient(
      30.11deg,
      rgba(255, 255, 255, 0.3) 54.41%,
      rgba(255, 238, 147, 0.3) 72.74%,
      rgba(233, 255, 216, 0.3) 86.69%
   ); */

   border-radius: 20px;
}
/* mobile screens */
@media screen and (max-width: 480px) {
   .multiCardImg {
      height: 130px;
      border-radius: 5px;
      /* border: 1px solid white; */
      filter: contrast(125%);
      box-shadow: 5px 5px 15px 5px rgb(0 0 0 / 30%);
      transition: all 0.25s ease-in-out;
   }
   .multidrinkName {
      font-family: 'Montserrat', sans-serif;
      font-weight: 500;
      font-size: 15px;
      margin: 5px 0 40px 0;
      width: 100px;
      cursor: pointer;
      transition: font-weight 0.7s ease-in-out;
   }
}
